<style lang="scss">

$color: #2D72FF;
.component-date{
  .slideDown-enter-active, .slideDown-leave-active {
    transition: all .2s;
  }
  li{
    list-style-type: none;
  }
  a{
    color: #000;
    text-decoration: none;
  }
  .slideDown-enter, .slideDown-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateY(100%);
  }
  .my-vue-date2{
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      background: #fff;
      font-size: 15px;
      padding: 5px;
      z-index: 999;
      .date-header{
        margin-top: 10px;
        text-align: center;
        position:relative;
        height: 30px;
        line-height: 30px;
        font-size: 20px;
        .last,.next{
          width: 25px;
          height: 30px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          cursor: pointer;
          font-size: 24px;
          background: url(./btn.png) center no-repeat;
          background-size: auto 70%;
        }
        .last{
          left: 50px;
          transform: translateY(-50%) rotate(180deg);
        }
        .next{
          right: 50px;
          
        }
      }
      table{
        width: 100%;
        th{
          font-weight: bold;
        }
        td,th{
          text-align:  center;
          height: 35px;
        }
        .today{
          span{
            color:$color;
            font-weight: bold;
          }
          .active{
            background: $color;
            font-weight: normal;
            color:#fff;
          }
        }
        .grey{
          color:#c0c4cc;
        }
        .active{
          background: $color;
          color:#fff;
        }
        .has{
          background: #bad1ff;
          color: #fff;
        }
        span{
          display:inline-block;
          width: 30px;
          height: 30px;
          line-height: 30px;
          border-radius: 50%;
          text-align: center;
          cursor: pointer;
        }
      }
    }
    .full-cover{
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.6);
      z-index: 999;
    }
    .btn-header{
      height: 30px;
      line-height: 30px;
      padding: 0 15px 5px;
      border-bottom: 1px solid #ececec;
      a{
        float: left;
        font-size: 15px;
      }
      .right{
        float: right;
        color: $color;
      }
    }
    .years{
      padding: 20px 0;
      li{
        float: left;
        width: 24%;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        text-align: center;
        border-radius: 2px;
        margin-left: 1%;
        margin-top: 15px;
        margin-bottom: 15px;

      }
      .active{
        color: $color;
        font-weight: bold;
      }
    }
}
</style>

<template>
<div class="component-date" :class="customClass">
  <div class="full-cover" v-if="showDialog" @click="close" ></div>
  <transition name="slideDown" mode="out-in" >
    <div class="my-vue-date2 "  v-if="showDialog" >
      <div class="btn-header">
        <a href="javascript:;" @click="close">取消</a>
        <a class="right" href="javascript:;" @click="send">确定</a>
      </div>
      <div class="date-header">
        <span class="last" @click="last" ></span>
        <span @click="showYear" >{{year}}</span>
        年
        <span @click="showMonth" >{{month}}</span>
        月
        <span class="next" @click="next"></span>
      </div>

      <ul class="years" v-if="yearDialog">
        <li :class="year == val ? 'active': ''" v-for="val in yearList" :key="val" @click="getYear(val)" >{{val}}</li>
      </ul>

      <ul class="years" v-if="monthDialog">
        <li :class="month == i ? 'active': ''" v-for="i in 12" :key="i" @click="getMonth(i)" >
          {{i}}月
        </li>
        
      </ul>

      <table v-if="!yearDialog && !monthDialog">
        <tr>
          <th>日</th>
          <th>一</th>
          <th>二</th>
          <th>三</th>
          <th>四</th>
          <th>五</th>
          <th>六</th>
        </tr>
        <tr v-for="(row,i) in rows" :key="i">
          <td v-for="(cell,n) in row" :key="n" :class="cell.date == nowDate ? 'today' : ''" >
            <span 
            class="date-item"
            :class="{grey: (cell.month == 'last' ) , active: hasChoosed(cell.date), has: isOlded(cell.date) }"
            :title="cell.date" 
            @click="getDate(cell,$event)" >{{cell.text}}</span>
          </td>
        </tr>
      </table>
    </div>
  </transition>
</div>
  
</template>

<script>
    export default {
      name: 'datePicker',
      props:{
        separator: {
          type: String,
          default: '-'
        },
        customClass: {
          type: String,
          default: ''
        },
        value: {
          type: [Array,String],
          default: ''
        },
        type: {
          type: String,
          default: 'date'
        },
        range: {  // ['2019-03-31','2019-05-01']
          type: Array,
          default(){
            return null
          }
        }
      },
        data(){
            return{
              showDialog: false,
              yearDialog: false,
              yearList: [],
              monthDialog: false,
              year: 2018,
              month: 8,
              dateLength: 0,
              startDay: 0,
              rows:[],
              date: [],
              nowMonth: 0,
              nowDate: '',
              minTime: '',
              maxTime: ''
            }
        },
        created(){

        },
        mounted(){
          this.year = new Date().getFullYear()
          this.month = new Date().getMonth() + 1
          this.nowMonth = this.month
          this.startDay = new Date(this.year+'/'+this.month+'/01').getDay()
          this.dateLength = this.getDateLength(this.month)
          this.nowDate = this.format(this.year + this.separator + this.month + this.separator + new Date().getDate())

          for(let i=0;i<12;i++){
            this.yearList.push(this.year-i)
          }

          //if(this.type == 'range'){ // type 为 range 时 可以传入默认时间 
            if(this.range && this.range.length == 2){
              this.minTime = new Date(this.range[0].split(this.separator).join('/')).getTime()
              this.maxTime = new Date(this.range[1].split(this.separator).join('/')).getTime()
            }
          //}
          if(this.value != ''){
            this.date = typeof(this.value) == 'string' ? [this.value] : this.value 
            this.year = this.date[0].split(this.separator)[0]
            this.month = parseInt(this.date[0].split(this.separator)[1])
          }
          
          this.getRows()
        },
        methods:{
          showYear(){
            this.monthDialog = false
            this.yearDialog = true
          },
          showMonth(){
            this.yearDialog = false
            this.monthDialog = true
          },
          show(){
            this.showDialog = true
          },
          close(){
            this.showDialog = false
            //this.clear()
          },
          getYear(year){
            this.year = year
            this.yearDialog = false
            this.startDay = new Date(this.year+'/'+this.month+'/01').getDay()
            this.getRows()
          },
          getMonth(month){
            this.month = month
            this.monthDialog = false
            this.startDay = new Date(this.year+'/'+this.month+'/01').getDay()
            this.getRows()
          },
          getDateLength(month){ // 获取每个月份天数
            if(month == 1 || month == 3 || month == 5 || month == 7 || month == 8 || month == 10 || month == 12){
              return 31
            }else if(month == 2){
              if (this.year % 4 === 0 && this.year % 100 !== 0 || this.year % 400 === 0) {
                return 29;
              } else {
                return 28;
              }
            }else{
              return 30
            }
          },
          getRows(){ // 获取月份天数列表数组
            var arr = []
            var index = 0
            var lastMonth = this.month - 1 == 0 ? 12 : this.month - 1
            var lastDay = this.getDateLength(lastMonth)
            var date
            //lastDay = lastDay == 0 ? 12 : lastDay
            if(this.startDay != 0){ // 月初第一天不是周一
              for(let d = this.startDay;d > 0;d--){
                let obj = {}
                obj.text = lastDay - d + 1
                obj.month = "last"
                if(this.month > 1){
                  date = this.year+this.separator+(this.month-1)+this.separator+(lastDay - d + 1)
                }else{
                  date = (this.year - 1)+this.separator+'12'+this.separator+(lastDay - d + 1)
                }
                obj.date = this.format(date)
                arr.push(obj)
              }
            }else{  // 月初第一天是周一 从第二行开始
              for(let d = 7;d > 0;d--){
                let obj = {}
                obj.text = lastDay - d + 1
                obj.month = 'last'

                if(this.month > 1){
                  date = this.year+this.separator+(this.month-1)+this.separator+(lastDay - d + 1)
                }else{
                  date = (this.year - 1)+this.separator+'12'+this.separator+(lastDay - d + 1)
                }
                obj.date = this.format(date)
                arr.push(obj)
              } 
            }
            for(let date=0;date<this.getDateLength(this.month);date++){ //  懒得写注释了 自己看吧
              let obj = {}
              obj.text = date + 1
              obj.month = "now"
              obj.date = this.format(this.year+this.separator+this.month+this.separator+(date + 1))
              arr.push(obj) 
            }
            for(let end=this.startDay+this.getDateLength(this.month);end<42;end++){
              let obj = {}
              index += 1
              obj.text = index
              obj.month = "next"
              if(this.month < 12){
                date = this.year+this.separator+(this.month+1)+this.separator+(index)
              }else{
                date = (this.year+1)+this.separator+1+this.separator+(index)
              }
              obj.date = this.format(date)
              arr.push(obj) 
            }
            let row = []
            for(let i=0;i<42;i+=7){
              row.push(arr.splice(0,7))
            }
            this.rows = row
          },
          last(){
            if(this.month > 1){
              this.month -= 1
            }else{
              this.month = 12
              this.year -= 1
            }
            this.startDay = new Date(this.year+'/'+this.month+'/01').getDay()
            this.getRows()
          },
          hasChoosed(value){
            for(let val of this.date){
              if(val == value){
                return true
              }
            }
          },
          next(){
            if(this.month < 12){
              this.month += 1
            }else{
              this.month = 1
              this.year += 1
            }
            this.startDay = new Date(this.year+'/'+this.month+'/01').getDay()
            this.getRows()
          },
          getDate(cell){
            
            if(this.range && this.range.length == 2){
              var t = new Date(cell.date.split(this.separator).join('/')).getTime()
              if(t < this.minTime){
                this.$emit('limit',{
                  type: 'less',
                  range: this.range
                })
                return
              }
              if(t > this.maxTime){
                this.$emit('limit',{
                  type: 'more',
                  range: this.range
                })
                return
              }
            }


            if(this.type == 'range'){
              if(this.date.length < 2){
                if(!this.date.find((val) => cell.date == val)){ 
                  this.date.push(cell.date)
                }else{
                  this.date.splice(0,1)
                }
              }else{
                var index = this.date.findIndex(( val )=> cell.date == val )
                if(index > -1){
                  this.date.splice(index,1)
                }else{
                  this.date[1] = cell.date
                }
                
              }
              this.date.sort((a,b) => { // 排序
                  var date1 = new Date(a.split(this.separator).join('/')).getTime()
                  var date2 = new Date(b.split(this.separator).join('/')).getTime()
                  return date1 - date2
              })
              
            }else{
              this.date = []
              this.date[0] = cell.date
            }
            
          },
          clear(){
            this.date = [];
          },
          send(){
            if(this.type == 'range'){
              this.$emit('choose',this.date)
              this.$emit('input',this.date)
            }else{
              this.$emit('choose',this.date[0])
              this.$emit('input',this.date[0])
            }
            this.close()
          },
          format(date){
            var arr = date.split(this.separator)
            arr[1] = arr[1] > 9 ? arr[1] : '0'+arr[1]
            arr[2] = arr[2] > 9 ? arr[2] : '0'+arr[2]
            return arr.join(this.separator)
          },
          isOlded(date){ // 查询当前日期是否为已选
            if(this.date.length == 2){
              var now = new Date(date.split(this.separator).join('/')).getTime()
              var min = new Date(this.date[0].split(this.separator).join('/')).getTime()
              var max = new Date(this.date[1].split(this.separator).join('/')).getTime()
              if(now > min && now < max){
                return true
              }else{
                return 
              }
            }
            return
          },
          getTimeNum(string){
            var date = string.split(this.separator).join('/')
            return new Date(date).getTime()
          }
        }

        
    }
</script>
